import Swal from 'sweetalert2';

export function swalFn(title, icon, toast = false, posi = 'center') {
  const txt = {
    position: posi,
    title,
    icon,
    timer: 1500,
    showConfirmButton: false,
    timerProgressBar: true,
    toast,
  };
  Swal.fire(txt);
}

export function errorSwalFn(title, text, toast = false, posi = 'center') {
  const txt = {
    position: posi,
    title,
    text,
    icon: 'error',
    timer: 1500,
    showConfirmButton: false,
    timerProgressBar: true,
    toast,
  };
  Swal.fire(txt);
}

export function checkLoginErrorSwalFn(title, icon) {
  const txt = {
    title,
    text: ' 3 ',
    icon,
    timer: 3000,
    showConfirmButton: false,
    timerProgressBar: true,
  };
  Swal.fire(txt);
}

export function passwordMassageSwalFn() {
  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: 'btn btn-secondary text-primary shadow-sm px-2',
    },
    buttonsStyling: false,
  });

  const txt = {
    position: 'top-center',
    title: '',
    text: '',
    icon: 'question',
    timer: 5000,
    showConfirmButton: true,
    confirmButtonText: '',
    closeOnClickOutside: true,
  };
  swalWithBootstrapButtons.fire(txt);
}

export function delSwalFn(data, methods, action) {
  const { title, id } = data;
  let newTitle = title;

  const variable = {
    id,
    action,
  };

  if (title === undefined) {
    newTitle = id;
  }

  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: 'btn btn-light border-gray me-2 px-2',
      cancelButton: 'btn btn-danger px-2',
    },
    buttonsStyling: false,
  });

  const txt = {
    title: ` [${newTitle}] `,
    text: '',
    icon: 'warning',
    showConfirmButton: true,
    confirmButtonText: '',
    showCancelButton: true,
    cancelButtonText: '',
  };

  swalWithBootstrapButtons.fire(txt)
    .then((result) => {
      if (result.isConfirmed) {
        methods(variable);
      } else {
        swalFn('', 'error');
      }
    });
}

